import React from "react";
import "./LogeedLayouts.scss";

export function LogeedLayouts(pros) {
  const { children } = pros;
  return (
    <div className="logeedLayout">
      <h1>LogeedLayouts</h1>
      <div className="logeedLayout__Content">
        <div className="logeedLayout__LefMenu">
          <p>Lef menu</p>
        </div>
        <div className="logeedLayout__ChildrenContent">
          <div className="logeedLayout__TopBar">
            <p>TOP BAR</p>
          </div>
          {children}
        </div>
      </div>
      <div className="logeedLayout__Footer">
        <p>Footer</p>
      </div>
    </div>
  );
}
